@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@400;700&display=swap");
:root {
  --clr-orange: hsl(25, 97%, 53%);
  --clr-white: hsl(0, 0%, 100%);
  --clr-light-grey: hsl(217, 12%, 63%);
  --clr-medium-grey: hsl(216, 12%, 54%);
  --clr-dark-blue: hsl(213, 19%, 18%);
  --clr-very-dark-blue: hsl(216, 12%, 8%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

button {
  cursor: pointer;
}

#root {
  font-family: "Overpass", sans-serif;
  font-size: 15px;
  margin: 0;
  background-color: var(--clr-very-dark-blue);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
}

.card {
  background-color: var(--clr-dark-blue);
  width: min(100% - 3rem, 20rem);
  padding: 1rem;
  border-radius: 1rem;
  color: var(--clr-light-grey);
  margin-bottom: 2rem;
}
.card .rating-state h2 {
  color: var(--clr-white);
}
.card .rating-state h2::before {
  content: "";
  width: 2.5rem;
  height: 2.5rem;
  display: block;
  background-color: #3e4b5b;
  background-image: url(../images/icon-star.svg);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  margin-bottom: 1rem;
}
.card .rating-state .btn-wrapper {
  display: flex;
  justify-content: space-between;
  margin-block: 2rem;
}
.card .rating-state .btn-wrapper .button-rating {
  background-color: #3e4b5b;
  border: none;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  color: inherit;
}
.card .rating-state .btn-wrapper .button-rating:hover {
  background-color: var(--clr-medium-grey);
}
.card .rating-state .btn-wrapper .button-rating:focus {
  background-color: var(--clr-orange);
}
.card .rating-state .btn-wrapper .button-rating:active {
  transform: translateY(2px);
}
.card .rating-state .button-submit {
  background-color: var(--clr-orange);
  border: none;
  padding-block: 1em;
  margin-bottom: 1rem;
  color: var(--clr-white);
  width: 100%;
  border-radius: 2em;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.card .rating-state .button-submit:hover {
  background-color: var(--clr-white);
  color: var(--clr-orange);
}

.thankyou-state {
  text-align: center;
}
.thankyou-state .your-rating {
  color: var(--clr-orange);
  background-color: #3e4b5b;
  border-radius: 1.5rem;
  display: inline-block;
  padding-inline: 1rem;
  padding-block: 0.5rem;
}
.thankyou-state::before {
  content: "";
  width: 100%;
  height: 8rem;
  display: block;
  background-color: #3e4b5b;
  background-image: url(../images/illustration-thank-you.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.attribution {
  font-size: 11px;
  text-align: center;
  color: white;
}
.attribution a {
  color: #5071f7;
}
